.App {
  font-family: sans-serif;
  text-align: center;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
}

.Ingredents {
  height: 300px;
  width: 250px;
  border-radius: 12px;
  box-shadow: 2px 2px 8px #888888;
  display: flex;
  border: 1px solid;
  border-color: black;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
  text-align: center;
  flex-direction: column;
  transition: 0.3s;
}

.Ingredents:hover {
  height: 310px;
  width: 260px;
}

.Ingredents:hover .steps {
  margin-bottom: 0px;
}

.steps {
  overflow-y: scroll;
  font-size: 12px;
  border-top: 600px;
  visibility: initial;
}

.App Button {
  margin: 20px;
  border: 1px solid black;
}

.Ingredents img {
  height: 150px;
  width: 220px;
  border-radius: 20px;
  margin: 10px;
}

.Ingredents p {
  font-size: 14px;
  margin: 0px;
}
